<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 col-md-6 col-lg-4">
        <div class="row pt-3 pb-3">
          <div class="col-6">
            <a href="/cart/" class="btn btn-primary pt-2 pb-2 w-100 text-light"><img src="/assets/img/nav/cart_icon.png" height="20"> ตะกร้าสินค้า</a>
          </div>
          <div class="col-6">
            <button class="btn btn-danger pt-2 pb-2 w-100 logoutFB_click" @click="fbLogout"><img src="/assets/img/nav/logout_icon.png" height="20"> ออกจากระบบ</button>
          </div>
        </div>
        <div class="row">
          <div class="col pb-3">
            <div class="card">
              <h5 class="card-header">ข้อมูลผู้ใช้</h5>
              <profile-form :name="name" :email="email" :address="address"></profile-form>
            </div>
          </div>
        </div>
      </div>
      <div class="col pt-3 pb-3">
        <div class="card">
          <h5 class="card-header">ประวัติการสั่งซื้อ</h5>
          <div class="card-body">
            <div v-if="order.length <= 0" class="alert alert-info" role="alert">
              <span><strong>ไม่มีรายการสั่งซื้อ</strong></span>
            </div>
            <template v-else v-for="(suborder, index) in order">
              <h5 class="card-header" :key="index">{{ suborder.order_code }} <span class="badge" :class="suborder.status_class"><span id="i_order_status">{{ suborder.status_str }}</span></span> <span v-if="suborder.tracking_no != null" class="badge badge-primary">เลขแทร็ก: {{ suborder.tracking_no }}</span></h5>
              <div class="card-body" :key="index">
                <div class="row">
                  <div class="col-12 col-lg-auto pb-1">
                    <table>
                      <tr><td>วันที่</td><td>{{ suborder.order_time }}</td></tr>
                      <tr><td>ยอดรวม&nbsp;</td><td>฿{{ suborder.total_price }}</td></tr>
                      <tr><td colspan="2"><a :href="'/order/' + suborder.order_code" target="_blank">{{ suborder.link_str }}</a></td></tr>
                    </table>
                  </div>
                  <div class="col">
                    <div class="row">
                      <div v-for="(subproduct, index2) in suborder.product" :key="index2" class="col-12 col-xl-6 pb-1">
                        <div class="row">
                          <div class="img_bg_cov" style="width: 75px;height: 75px;display: inline-block;" :style="'background-image: url(\'/assets/img/product/s_' + subproduct.picture_file + '\');'"></div>
                          <div class="col">
                            <div class="row">
                              <div class="col">
                                {{ subproduct.product_name }}
                              </div>
                            </div>
                            <div class="row">
                              <div class="col-6 text-right mw100px">
                                {{ subproduct.amount }} ชิ้น
                            </div>
                              <div class="col-6 text-right mw100px">
                                ฿{{ subproduct.amount * subproduct.price }}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProfileForm from './profile-form.vue'

export default {
  name: 'Profile',
  components: {ProfileForm},
  data () {
    return {
      name: '',
      email: '',
      address: {
        name: '', tel: '', place: '', subdistrict: '', district: '', province: '', post: ''
      },
      order: []
    }
  },
  computed: {
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getProfileInfo'}).then(res => {
      let that = res.body
      if (that.fail) window.location.replace('/')
      this.name = that.name
      this.email = that.email
      this.address = that.address
      this.order = that.order
    })
  },
  mounted () {
  },
  methods: {
    fbLogout () {
      this.$http.post('/api/vpost/', {action: 'logout'}).then(res => window.location.replace('/'))
    }
  }
}
</script>

<style scoped></style>



