<template>
  <div class="col pt-5 pl-3 pb-0 pr-3 bg-light text_wrap">
    <h1>วิธีการสั่งซื้อ</h1>
    <h4 class="ml-4 text-primary">เลื่อนเพื่อดู >>></h4>
    <div class="row m-0 p-0">
      <div class="col-12 mt-3 mb-3 div_hori" id="div_hori_recom" @wheel="horiScroll($event, 'div_hori_recom')" style="box-shadow: 0px -3px 3px rgba(0,0,0,.3);"><img style="max-height: 90vh;" src="//idolm.art/assets/img/nav/how.png"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'How',
  data () {
    return {
    }
  },
  methods: {
    horiScroll (event, id) {
      if (document.getElementById(id).scrollWidth === document.getElementById(id).clientWidth) return
      event.preventDefault()
      if (event.deltaX !== 0) document.getElementById(id).scrollLeft += event.deltaX
      else if (event.deltaY !== 0) document.getElementById(id).scrollLeft += event.deltaY
    }
  }
}
</script>

<style scoped></style>



