import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/components/Landing'
import About from '@/components/About'
import How from '@/components/How'
import Cart from '@/components/Cart'
import Order from '@/components/Order'
import Product from '@/components/Product'
import Profile from '@/components/Profile'
import Policy from '@/components/Policy'
import Search from '@/components/Search'
import Terms from '@/components/Terms'
import NotFound from '@/components/NotFound'

import idx2 from '@/components/idx2'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing
    },
    {
      path: '/about/',
      name: 'About',
      component: About
    },
    {
      path: '/how/',
      name: 'How',
      component: How
    },
    {
      path: '/cart/',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/order/:code',
      name: 'Order',
      component: Order
    },
    {
      path: '/product/:code',
      name: 'Product',
      component: Product
    },
    {
      path: '/profile/',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/policy/',
      name: 'Policy',
      component: Policy
    },
    {
      path: '/search/',
      name: 'Search',
      component: Search
    },
    {
      path: '/terms/',
      name: 'Terms',
      component: Terms
    },
    {
      path: '/idx2/',
      name: 'idx2',
      component: idx2
    },
    {
      path: '*',
      name: 'NotFound',
      component: NotFound
    }
  ]
})



