<template>
  <div class="container-fluid" style="margin-top: -58px;">
    <div class="row">
      <div class="col-12 pl-0 pr-0">
        <b-carousel id="carousel1"
                    style="text-shadow: 1px 1px 2px #333;"
                    :controls="caro.length > 1"
                    :indicators="caro.length > 1"
                    :interval="10000"
                    img-width="1024"
                    img-height="480"
                    v-model="slide"
                    @sliding-start="onSlideStart"
                    @sliding-end="onSlideEnd"
                    class="caro_hvw"
        >
          <b-carousel-slide v-for="(subcaro, index) in caro" :key="index">
            <img src="./../assets/logo.png" class="d-inline-block d-sm-none" style="margin-bottom: 80px;height: 30px;">
            <img src="./../assets/logo.png" class="d-none d-sm-inline-block d-md-none" style="margin-bottom: 120px;height: 38px;">
            <img src="./../assets/logo.png" class="d-none d-md-inline-block" style="margin-bottom: 200px;height: 58px;">
            <br>
            <div class="DBD_bg" id="Certificate-banners"></div>
            <br>
            <div class="d-inline-block" style="color: rgb(87, 64, 154);background-color: rgba(0,0,0,0.8);border-radius: 10px 10px 0 0;">&nbsp;กรมพัฒนาธุรกิจการค้า Trustmarkthai&nbsp;</div>
            <h5 style="background-color: rgba(0,0,0,0.8);border-radius: 0 0 10px 10px;">Idolm.art (ไอดอลมาร์ต) คือร้านค้าออนไลน์ที่จำหน่ายสินค้าไอดอล วงต่างๆ เช่น BNK48, Sweat16! เพื่อความสะดวกท่านสามารถติดตามข่าวสารได้ที่<a href="https://facebook.com/page.idolm.art/" target="_blank" class="text-light"><div class="d-inline-block"><u>แฟนเพจของเรา</u></div></a> และเพื่อความมั่นใจในการจับจ่ายใช้สอยของท่าน สามารถตรวจสอบได้ที่<a href="/about/" target="_blank" class="text-light"><div class="d-inline-block"><u>ข้อมูลร้านของเรา</u></div></a></h5>
            <a :href="subcaro.url" slot="img">
              <div class=" h-100 w-100 d-none d-sm-block">
                <div class="d-block h-100 w-100 img_bg_cov" :style="'background-image: url(\'/assets/img/caro/'+ subcaro.pic_l +'\');'"></div>
              </div>
              <div class="h-100 w-100 d-block d-sm-none">
                <div class="d-block h-100 w-100 img_bg_cov" :style="'background-image: url(\'/assets/img/caro/'+ subcaro.pic_s +'\');'"></div>
              </div>
            </a>
          </b-carousel-slide>
        </b-carousel>
      </div>
    </div>
    <div class="row pt-3">
      <div class="col-12 pl-0 pr-0">
        <h1 class="ml-3">รู้จักเราก่อน</h1>
        <div class="card-body pl-0 pr-0 pt-0">
          <div class="row ml-0 mr-0 pl-3 pr-0">
            <a href="/how/" class="text-danger">
              <div class="product_box mt-3 mb-3 mr-3 h-auto w-auto">
                <h5 class="mt-2 mr-2">วิธีการสั่งซื้อ</h5>
              </div>
            </a>
            <a href="//facebook.com/page.idolm.art/" target="_blank" class="text-primary">
              <div class="product_box mt-3 mb-3 mr-3 h-auto w-auto">
                <h5 class="mt-2 mr-2">เพจ Facebook</h5>
              </div>
            </a>
            <a href="/about/" class="text-success">
              <div class="product_box mt-3 mb-3 mr-3 h-auto w-auto">
                <h5 class="mt-2 mr-2">ข้อมูลร้าน</h5>
              </div>
            </a>
          </div>
        </div>
        <h1 class="ml-3">ของมันต้องมี</h1>
        <div class="card-body pl-0 pr-0 pt-0">
          <div class="row ml-0 mr-0 pl-0 pr-0 pt-0">
            <div class="col-12 div_hori" id="div_hori_recom" @wheel="horiScroll($event, 'div_hori_recom')">
              <a v-for="(subproduct, index) in recom.product" :key="index" :href="'/product/' + recom.result[subproduct].product_code + '/'" class="text-dark">
                <div class="product_box mt-3 mb-3 mr-3">
                  <div v-if="recom.result[subproduct].old_price != null || recom.result[subproduct].amount <= 0" class="promotion_badge">
                    <div class="badge_img" :class="{badge_img_dark: recom.result[subproduct].amount <= 0}">{{ recom.result[subproduct].amount <= 0 ? 'หมด' : 'SALE' }}</div>
                  </div>
                  <div class="product_img img_bg_cov" :style="'background-image: url(\'/assets/img/product/s_' + recom.result[subproduct].picture_file + '\');'"></div>
                  <h5 class="mt-2">{{ recom.result[subproduct].product_name }}</h5>
                  <span v-if="recom.result[subproduct].old_price != null" class="text-secondary" style="text-decoration: line-through;">฿{{ recom.result[subproduct].old_price_str }}
                  </span>
                  <span :class="{'text-danger': recom.result[subproduct].old_price != null, 'price_sale': recom.result[subproduct].old_price != null}">฿{{ recom.result[subproduct].price_str }}
                  </span>
                </div>
              </a>
            </div>
          </div>
        </div>
        <h1 class="ml-3">เมมเบอร์</h1>
        <div class="card-body pl-0 pr-0 pt-0">
          <template v-for="(gen, index) in member">
            <h6 class="ml-4 mb-0 text-secondary" :key="index">
              <u>รุ่น {{ index + 1 }}&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</u>
            </h6>
            <div class="row ml-0 mr-0 mb-3 pl-3 pr-0" :key="index">
              <a v-for="(submember, index2) in gen" :key="index2" :href="'/search?query=%23' + submember" class="text-secondary">
                <div class="product_box mt-1 mr-1 h-auto w-auto">
                  <h5 class="mt-2 mr-2">{{ submember }}</h5>
                </div>
              </a>
            </div>
          </template>
        </div>
        <h1 class="ml-3">หมวดหมู่</h1>
        <div class="card-body pl-0 pr-0 pt-0">
          <div class="row ml-0 mr-0 p-0">
            <div id="div_hori_cat" class="col-12 pb-3 div_hori" @wheel="horiScroll($event, 'div_hori_cat')">
              <a v-for="(subcat, index) in cat" :key="index" :href="'/search?query=%23' + subcat.name">
                <div class="category_box mt-3 mr-3">
                  <div class="img_bg_cov w-100 h-100" :style="'background-image: url(\'/assets/img/category/' + subcat.pic + '\');'"></div><br>
                  #{{ subcat.name }}
                </div>
              </a>
            </div>
          </div>
        </div>
        <h1 class="ml-3">สินค้าทั้งหมด</h1>
        <div class="card-body">
          <div class="row">
            <div v-for="(subproduct, index) in all" :key="index" class="col-6 col-sm-4 col-md-3 col-xl-2 p-1">
              <a :href="'/product/' + subproduct.product_code + '/'" class="text-dark">
                <div class="product_box_all">
                  <div class="img_bg_cov r169" :style="'background-image: url(\'/assets/img/product/s_' + subproduct.picture_file + '\');'"></div>
                  <div class="product_detail_all">
                      {{ subproduct.product_name }}<br>
                      <span v-if="subproduct.old_price != null" class="text-secondary" style="text-decoration: line-through;">฿{{ subproduct.old_price_str }}
                      </span>
                      <span :class="{'text-danger': subproduct.old_price != null, 'price_sale_mini': subproduct.old_price != null}">฿{{ subproduct.price_str }}
                      </span>
                      <span v-if="subproduct.amount <= 0" class="badge badge-dark">หมด</span>
                  </div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Landing',
  data () {
    return {
      caro: [],
      cat: [],
      recom: {product: []},
      all: [],
      pre_all: [],
      i_all: 0,
      member: []
    }
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getCaroInfo'}).then(res => {
      this.caro = res.body
    })
    this.$http.post('/api/vpost/', {action: 'getLandingInfo'}).then(res => {
      let that = res.body
      this.cat = that.cat
      this.recom = that.recom
      this.pre_all = that.all
      this.member = that.member
    })
    window.onscroll = () => {
      if (document.getElementById('app').offsetHeight - window.innerHeight - window.scrollY <= 70) {
        let fAll = this.i_all + 24
        for (; this.i_all < fAll && this.i_all < this.pre_all.length; this.i_all++) {
          this.all.push(this.pre_all[this.i_all])
        }
      }
    }
  },
  methods: {
    horiScroll (event, id) {
      if (document.getElementById(id).scrollWidth === document.getElementById(id).clientWidth) return
      event.preventDefault()
      if (event.deltaX !== 0) document.getElementById(id).scrollLeft += event.deltaX
      else if (event.deltaY !== 0) document.getElementById(id).scrollLeft += event.deltaY
    }
  }
}
</script>

<style scoped>
</style>



