<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col mt-3"><h1>ค้นหาสินค้า</h1></div>
    </div>
    <div class="row">
      <div class="col-12 col-lg-6">
              <form class="form-inline" action="/search/">
                  <div class="input-group w-100">
                      <input name="query" type="search" class="form-control rounded-0" placeholder="ค้นหา" aria-label="search" aria-describedby="basic-addon1" :value="!!query ? query : ''">
                      <button class="btn btn-primary rounded-0 my-sm-0" type="submit">&#8981;</button>
                  </div>
              </form>
      </div>
    </div>
    <div class="row">
      <div class="col-12 mt-2 pl-4">
        <span class="card-subtitle text-muted">แนะนำ: </span>
        <div v-for="(subtag, index) in tag" :key="index" class="div_ib"><a :href="'/search/?query=%23' + subtag">#{{ subtag }}</a>&nbsp;</div>
      </div>
    </div>
    <div class="row">
      <div class="col-12 pl-0 pr-0 mt-3">
        <template v-if="!!query">
          <h5 class="card-header">ผลการค้นหา {{ query }}</h5>
          <div class="card-body">
            <div class="row">
                <div v-for="(subproduct, index) in search.product" :key="index" class="col-6 col-sm-4 col-md-3 col-xl-2 p-1">
                  <a :href="'/product/' + search.result[subproduct].product_code + '/?origin=' + origin" class="text-dark">
                    <div class="product_box_all">
                      <div class="img_bg_cov r169" :style="'background-image: url(\'/assets/img/product/s_' + search.result[subproduct].picture_file + '\');'"></div>
                      <div class="product_detail_all">
                          {{ search.result[subproduct].product_name }}<br>
                          <span v-if="search.result[subproduct].old_price != null" class="text-secondary" style="text-decoration: line-through;">฿{{ search.result[subproduct].old_price_str }}
                          </span>
                          <span :class="{'text-danger': search.result[subproduct].old_price != null, 'price_sale_mini': search.result[subproduct].old_price != null}">฿{{ search.result[subproduct].price_str }}
                          </span>
                          <span v-if="search.result[subproduct].amount <= 0" class="badge badge-dark">หมด</span>
                      </div>
                    </div>
                  </a>
                </div>
            </div>
            <div v-if="search.product.length <= 0" class="row ml-3 mt-3 mr-3">
              <div class="col">
                <div class="alert alert-light" role="alert">
                    ไม่พบผลการค้นหา
                </div>
              </div>
            </div>
          </div>
        </template>
        <h1 class="ml-3">ของมันต้องมี</h1>
        <div class="card-body pl-0 pr-0 pt-0">
          <div class="row ml-0 mr-0 pl-0 pr-0 pt-0">
            <div class="col-12 div_hori" id="div_hori_recom" @wheel="horiScroll($event, 'div_hori_recom')">
              <a v-for="(subproduct, index) in recom.product" :key="index" :href="'/product/' + recom.result[subproduct].product_code + '/?origin=' + origin" class="text-dark">
                <div class="product_box mt-3 mb-3 mr-3">
                  <div v-if="recom.result[subproduct].old_price != null || recom.result[subproduct].amount <= 0" class="promotion_badge">
                    <div class="badge_img" :class="{badge_img_dark: recom.result[subproduct].amount <= 0}">{{ recom.result[subproduct].amount <= 0 ? 'หมด' : 'SALE' }}</div>
                  </div>
                  <div class="product_img img_bg_cov" :style="'background-image: url(\'/assets/img/product/s_' + recom.result[subproduct].picture_file + '\');'"></div>
                  <h5 class="mt-2">{{ recom.result[subproduct].product_name }}</h5>
                  <span v-if="recom.result[subproduct].old_price != null" class="text-secondary" style="text-decoration: line-through;">฿{{ recom.result[subproduct].old_price_str }}
                  </span>
                  <span :class="{'text-danger': recom.result[subproduct].old_price != null, 'price_sale': recom.result[subproduct].old_price != null}">฿{{ recom.result[subproduct].price_str }}
                  </span>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Search',
  data () {
    return {
      query: this.$route.query.query,
      request_uri: this.$route.fullPath,
      origin: window.location.href,
      max_search: 5,
      tag: [],
      search: {product: []},
      recom: {product: []}
    }
  },
  computed: {
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getSearchInfo', query: this.$route.query.query}).then(res => {
      let that = res.body
      this.search = that.search
      this.recom = that.recom
      this.tag = that.tag
    })
  },
  mounted () {
  },
  methods: {
    horiScroll (event, id) {
      if (document.getElementById(id).scrollWidth === document.getElementById(id).clientWidth) return
      event.preventDefault()
      if (event.deltaX !== 0) document.getElementById(id).scrollLeft += event.deltaX
      else if (event.deltaY !== 0) document.getElementById(id).scrollLeft += event.deltaY
    }
  }
}
</script>

<style scoped></style>



