<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col mt-3"><h1>ตะกร้าสินค้า</h1></div>
    </div>
    <div class="row">
      <div v-for="(subproduct, index) in product" :key="index" class="col-12 col-md-6 col-lg-4 card border-primary rounded-0 bg-light i_product_item" :data-code="subproduct.product_code">
        <div class="del_product del_product_click" :data-code="subproduct.product_code" @click="delCart(subproduct.product_code)">X</div>
        <div class="row h-100">
          <div class="col-3 img_bg_cov i_product_img" :style="'background-image: url(\'/assets/img/product/s_' + subproduct.picture_file + '\');'"></div>
          <div class="col-9 card-body text-primary">
            <div class="row">
              <div class="col">
                <h5 class="i_product_name">{{ subproduct.product_name }}</h5>
              </div>
            </div>
            <div class="row">
              <div class="col-6">
                <form onsubmit="return false;">
                  <div class="input-group">
                    <input type="number" class="form-control i_amount" v-model="subproduct.amount" min="1" :max="subproduct.max_amount" required :data-code="subproduct.product_code" :data-price="subproduct.price" @change="editCart(subproduct.product_code, subproduct.amount)">
                    <div class="input-group-append">
                      <span class="input-group-text">ชิ้น</span>
                    </div>
                  </div>
                </form>
              </div>
              <div class="col-6"><h5 class="text-right mt-2 mr-2">฿<span class="item_subtotal">{{ product_price[index] }}</span></h5></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="isCount" class="row border-top pt-2 mt-2">
      <div class="col-12 col-md-6 col-lg-4 card border-primary rounded-0 bg-light">
        <div class="row">
          <div class="col-7 card-body text-primary">
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text">ค่าจัดส่ง</span>
              </div>
              <select id="delivery_sel" name="delivery_sel" class="form-control" v-model="delivery_type" @change="editDelivery(delivery_type)">
                 <option v-for="(subdelivery, index) in delivery_str" :key="index" :value="index" :selected="delivery_type == index">{{ subdelivery }}</option>
              </select>
            </div>
          </div>
          <div class="col-5 card-body text-primary">
            <h5 class="text-right mt-2 mr-2">฿<span id="delivery_fee">{{ delivery_fee }}</span></h5>
          </div>
        </div>
      </div>
    </div>
    <div v-if="isCount" class="row border-top pt-2 mt-2 pb-2">
      <div class="col-12 col-md-6 col-lg-4 card border-success rounded-0 bg-light">
        <div class="row">
          <div class="col-7 card-body text-success">
            <h5 class=" ml-2 mt-2">รวมทั้งหมด</h5>
          </div>
          <div class="col-5 card-body text-success">
            <h5 class="text-right mt-2 mr-2">฿<span id="total_price">{{ total_price }}</span></h5>
          </div>
        </div>
      </div>
      <div class="col-12 col-lg-8 mt-2" :class="{hidden: err_hide}" id="err_box">
        <div class="alert alert-danger" role="alert">
          <span id="err_txt" v-html="err_html"></span>
        </div>
      </div>
      <div class="col-12 col-lg-8">
        <div class="row">
          <div class="col-8 card-body"><h6><span id="disclaimer_text">เมื่อกดสั่งซื้อแล้ว หากไม่ส่งหลักฐานการโอนเงิน <span class="text-danger">ภายใน {{order_exp}} ชั่วโมง</span> รายการสั่งซื้อจะถูกยกเลิก</span></h6></div>
          <div class="col-4 mt-2">
            <div class="row">
              <div class="col-auto my-1">
                <div class="custom-control custom-checkbox mr-sm-2">
                  <input type="checkbox" class="custom-control-input" id="disclaimer_check" name="disclaimer_check" v-model="isAgree">
                  <label class="custom-control-label" for="disclaimer_check">ยอมรับ</label>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col"><button type="button" class="btn btn-success" id="buy_click" :disabled="!isAgree" @click="orderCart">สั่งซื้อ</button></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="!isCount" class="row">
      <div class="col">
        <div id="no_product" class="alert alert-info" role="alert">
          <strong>ไม่มีสินค้าในตะกร้า</strong>
          <a href="/">กลับหน้าแรก</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Cart',
  data () {
    return {
      product: [],
      isCount: false,
      delivery_type: 'REG',
      delivery_str: {},
      delivery_price: {},
      order_exp: '',
      isAgree: false,
      err_hide: true,
      err_html: ''
    }
  },
  computed: {
    product_price () {
      return this.product.map((x) => x.amount * x.price)
    },
    delivery_fee () {
      return this.delivery_price[this.delivery_type]
    },
    total_price () {
      return this.product_price.reduce((a, b) => a + b) + this.delivery_fee
    }
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getCartInfo'}).then(res => {
      let that = res.body
      if (that.fail) window.location.replace('/')
      this.product = that.product
      this.isCount = that.isCount
      this.delivery_type = that.delivery_type
      this.delivery_str = that.delivery_str
      this.delivery_price = that.delivery_price
      this.order_exp = that.order_exp
    })
  },
  mounted () {
  },
  methods: {
    delCart (code) {
      this.$http.post('/api/vpost/', {action: 'delCart', code: code}).then(res => location.reload(true))
    },
    editCart (code, amount) {
      this.$http.post('/api/vpost/', {action: 'editCart', code: code, amount: amount}).then(res => {
        this.delivery_price = res.body.delivery
      })
    },
    editDelivery (type) {
      this.$http.post('/api/vpost/', {action: 'editDelivery', type: type}).then(res => {})
    },
    orderCart () {
      this.$http.post('/api/vpost/', {action: 'orderCart', type: this.delivery_type}).then(res => {
        let that = res.body
        if (that.success) {
          window.location.replace('/order/' + that.order_code + '/')
        } else if (that.err_code === 'ERR_LIMIT') {
          this.err_html = '<strong>สินค้าต่อไปนี้มีจำนวนจำกัด</strong><br>'
          for (let i = 0; i < that.product.length; i++) {
            this.err_html += that.product[i].product_name + ' ไม่เกิน ' + that.product[i].amount + ' ชิ้น <br>'
          }
          this.err_hide = false
        } else {
          this.err_html = 'เกิดความผิดพลาดของระบบ โปรดติดต่อ support@idolm.art'
          this.err_hide = false
        }
      })
    }
  }
}
</script>

<style scoped>
</style>



