<template>
  <div class="container-fluid caro_h100">
    <div class="row caro_h100">
        <div class="col-md bg-dark pl-0 pr-0" id="product_caro">
          <a class="a_back text-light" :href="origin==null?'/':encodeURI(origin).replace(/#/g, '%23')"><div>&lt; ย้อนกลับ</div></a>
          <b-carousel id="carousel1"
                    style="text-shadow: 1px 1px 2px #333;"
                    controls
                    indicators
                    :interval="0"
                    img-width="1024"
                    img-height="480"
                    v-model="slide"
                    @sliding-start="onSlideStart"
                    @sliding-end="onSlideEnd"
                    class="caro_h350"
        >
          <b-carousel-slide v-for="(subpic, index) in picture_file" :key="index">
            <div slot="img" class="d-block h-100 w-100 img_bg" :style="'background-image: url(\'/assets/img/product/'+ subpic +'\');'"></div>
          </b-carousel-slide>
        </b-carousel>
        </div>
        <div class="col-md-4 ovy_auto">
          <div class="row">
            <div class="col mt-3">
              <h2 id="i_product_name" :data-code="product_code">
                {{ product_name }}
                <span v-if="old_price!=null" class="badge badge-danger">SALE</span>
              </h2>
            </div>
          </div>
          <div v-if="old_price!=null" class="row">
            <div class="col-12"><span class="ml-2 text-secondary">จากราคา <span style="text-decoration: line-through;">฿{{ old_price_format }}</span></span></div>
          </div>
          <div class="row">
            <div class="col-5 col-md-12 col-lg-5 mt-1 text-center">
            <button id="i_price" type="button" class="btn w-100" :class="old_price_class" disabled>฿{{ price_format }}</button>
            </div>
            <div class="col-7 col-md-12 col-lg-7 mt-1 text-center">
              <button id="i_isIn" :data-isIn="is_amount" type="button" class="btn w-100 h-100" :class="amount_class" disabled v-html="is_amount?'มีของพร้อมส่ง':'สินค้าหมด'"></button>
            </div>
          </div>
          <div class="row mt-3">
            <div class="col-5 text-center">
              <span class="text-info">รอหลุดจอง {{ book_amount }} ชิ้น</span>
            </div>
            <div class="col-7 text-center">
              <h5 :class="amount_txt_class">ซื้อได้เลย {{ amount }} ชิ้น</h5>
            </div>
          </div>
          <div class="row mt-3">
            <div id="i_tag_name" class="col text-primary" v-html="tag_name">
            </div>
          </div>
          <div class="row mt-3">
            <div class="col">
              <p id="i_product_description" v-html="product_description"></p>
            </div>
          </div>
            <div v-if="is_amount&&is_login" class="row">
              <div class="col text-center p-0">
                <button v-if="is_in_cart" type="button" class="list-group-item list-group-item-action rounded-0 border-right-0 h-100" disabled><img src="/assets/img/nav/correct_icon.png" height="20"> ลงตะกร้าแล้ว</button>
                <button v-else type="button" class="list-group-item list-group-item-action rounded-0 border-right-0 h-100 hover_pointer i_add_cart_click" @click="addCart"><img src="/assets/img/nav/cart_icon.png" height="20"> เพิ่มลงตะกร้า</button>
              </div>
              <div class="col text-center p-0">
                <button type="button" class="list-group-item list-group-item-action rounded-0 h-100 hover_pointer i_buy_now_click" @click="buyNow"><img src="/assets/img/nav/money_icon.png" height="20"> ซื้อเลย</button>
              </div>
            </div>
            <div v-else-if="is_amount" class="row">
              <div class="col text-center p-0">
                <button type="button" class="btn btn-primary rounded-0 w-100 loginFB_click" @click="fbLogin">
                  <img src="/assets/img/nav/fb_white.png" height="28"> ล็อกอินเพื่อซื้อสินค้า
                </button>
              </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Product',
  data () {
    return {
      product_code: this.$route.params.code,
      origin: this.$route.query.origin,
      picture_file: [],
      product_name: '',
      old_price: null,
      old_price_format: '',
      price: null,
      price_format: '',
      is_amount: false,
      tag_name: '',
      product_description: '',
      is_login: false,
      is_in_cart: false,
      amount: 0,
      book_amount: 0
    }
  },
  computed: {
    old_price_class () {
      return this.old_price == null
        ? {'btn-outline-primary': true}
        : {'btn-outline-danger': true, fs13em: true}
    },
    amount_class () {
      return this.is_amount
        ? {'btn-outline-success': true}
        : {'btn-outline-danger': true}
    },
    amount_txt_class () {
      return this.is_amount
        ? {'text-success': true}
        : {'text-danger': true}
    }
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getProductInfo', code: this.$route.params.code}).then(res => {
      let that = res.body
      if (that.fail) window.location.replace('/')
      this.picture_file = that.picture_file
      this.product_name = that.product_name
      this.old_price = that.old_price
      this.old_price_format = that.old_price_format
      this.price = that.price
      this.price_format = that.price_format
      this.is_amount = that.is_amount
      this.tag_name = that.tag_name
      this.product_description = that.product_description
      this.is_login = that.is_login
      this.is_in_cart = that.is_in_cart
      this.amount = that.amount
      this.book_amount = that.book_amount
    })
  },
  mounted () {
    document.getElementById('app').className += ' caro_h100'
  },
  methods: {
    fbLogin () {
      window.loginFB(window.location)
    },
    addCart () {
      this.$http.post('/api/vpost/', {action: 'addCart', code: this.product_code}).then(res => location.reload(true))
    },
    buyNow () {
      this.$http.post('/api/vpost/', {action: 'addCart', code: this.product_code}).then(res => window.location.replace('/cart/'))
    }
  }
}
</script>

<style scoped>
</style>



