<template>
  <div class="container-fluid">
    <div v-if="error != null" class="row">
      <div class="col mt-2">
        <div class="alert alert-danger" role="alert">
          <span id="block_error"><strong>{{ error }}</strong></span>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col mt-3"><h1>รายการสั่งซื้อ <span id="i_order_code">{{ order.order_code }}</span> <span class="badge" :class="order.status_class"><span id="i_order_status">{{ order.status_str }}</span></span> <span v-if="order.is_sent" class="badge badge-primary">เลขแทร็ก: {{ order.tracking_no }}</span><h1></div>
    </div>
    <div v-if="order.is_bill" class="row">
      <div class="col mt-2">
        <div class="alert alert-success" role="alert">
          <strong>ได้รับหลักฐานการชำระเงินแล้ว</strong>
          <p>ขอบคุณที่สั่งสินค้ากับ Idolm.art เราจะดำเนินการตรวจสอบหลักฐาน จัดส่งสินค้าและแจ้งเลขแทร็กภายใน 48 ชั่วโมง ในระหว่างนี้ท่านสามารถเช็คสถานะสินค้าของท่านได้ตามวิธีการด้านล่างนี้ หรือรอเลขแทร็กจากอีเมลของท่านคือ {{ order.email }}</p>
          <p class="text-center"><img style="max-width: 100%; max-height: 90vh;" src="../assets/rw_success.png"></p>
        </div>
      </div>
    </div>
    <div class="row">
      <div v-for="(subproduct, index) in order.product" :key="index" class="col-12 col-md-6 col-lg-4 card border-primary rounded-0 bg-light i_product_item" :data-code="subproduct.product_code">
        <div class="row h-100">
          <div class="col-3 img_bg_cov i_product_img" :style="'background-image: url(\'/assets/img/product/s_' + subproduct.picture_file + '\');'"></div>
          <div class="col-9 card-body text-primary">
            <div class="row">
              <div class="col">
                <h5 class="i_product_name">{{ subproduct.product_name }}</h5>
              </div>
            </div>
            <div class="row">
              <div class="col-6">
                <h5 class="text-right mt-2 mr-2 i_amount" :data-amount="subproduct.amount" :data-price="subproduct.price">{{ subproduct.amount }} ชิ้น</h5>
            </div>
              <div class="col-6">
                <h5 class="text-right mt-2 mr-2 item_subtotal">฿{{ subproduct.amount * subproduct.price }}</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row border-top pt-2 mt-2">
      <div class="col-12 col-md-6 col-lg-4 card border-primary rounded-0 bg-light">
        <div class="row">
          <div class="col-8 card-body text-primary">
            <h5 class="ml-2 mt-2">ค่าจัดส่ง {{ order.delivery_str }}</h5>
          </div>
          <div class="col-4 card-body text-primary">
            <h5 class="text-right mt-2 mr-2" id="i_delivery_fee" :data-fee="order.delivery_fee">฿{{ order.delivery_fee }}</h5>
          </div>
        </div>
      </div>
      <div v-if="order.is_discount" class="col-12 col-md-6 col-lg-4 card border-primary rounded-0 bg-light">
        <div class="row">
          <div class="col-7 card-body text-primary">
            <h5 class=" ml-2 mt-2">ส่วนลด</h5>
          </div>
          <div class="col-5 card-body text-primary">
            <h5 class="text-right mt-2 mr-2" id="i_discount" :data-discount="order.discount">฿-{{ order.discount }}</h5>
          </div>
        </div>
      </div>
    </div>
    <div class="row border-top pt-2 mt-2">
      <div class="col-12 col-md-6 col-lg-4 card border-success rounded-0 bg-light">
        <div class="row">
          <div class="col-7 card-body text-success">
            <h5 class=" ml-2 mt-2">รวมทั้งหมด</h5>
          </div>
          <div class="col-5 card-body text-success">
            <h5 class="text-right mt-2 mr-2" id="total_price">฿{{ order.total_price }}</h5>
          </div>
        </div>
      </div>
    </div>
    <div v-if="!order.is_fail" class="row border-top pt-2 mt-2">
      <div class="col mb-3">
        <div class="card">
          <h5 class="card-header">ชื่อ เบอร์โทรศัพท์ ที่อยู่ และรหัสไปรษณีย์</h5>
          <profile-form :name="order.name" :email="order.email" :address="order.address" :orderonly="true" :readonly="!order.is_book"></profile-form>
        </div>
      </div>
      <div class="col-12 col-md-6 col-lg-8 mb-3">
        <div class="card">
          <h5 class="card-header">การชำระเงิน</h5>
          <div class="card-body text-center">
            <div v-if="order.is_book" class="row mt-3 text-left pay_choice" id="choice_transfer">
              <div class="col">
                <form id="form_transfer" @submit.prevent="submitTransfer">
                  <input type="hidden" name="action" value="confirmTransfer">
                  <input type="hidden" name="order_code" :value="order.order_code">
                  <input type="hidden" name="order_address" :value="order.address_str">

                  <p><span class="text-danger">โอนเงิน</span>มายังบัญชีใดบัญชีหนึ่งต่อไปนี้</p>
                  <ul>
                      <li v-for="(subbank, index) in order.bank" :key="index">{{ subbank }}</li>
                  </ul>
                  <p>จากนั้น<span class="text-danger">อัพโหลดภาพหลักฐานการโอนเงิน</span> ภายในวันที่<span class="text-info"> {{ order.expire_str }}</span></p>
                  <div v-show="false">
                    <div class="input-group">
                        <div class="input-group-prepend">
                          <span class="input-group-text w140px">ธนาคารปลายทาง</span>
                        </div>
                      <select class="form-control" name="target_bank" required>
                            <option v-for="(subbank, index) in order.bank" :key="index" :value="index">{{ subbank }}</option>
                      </select>
                    </div>
                    <div class="input-group">
                        <div class="input-group-prepend">
                          <span class="input-group-text w140px">วันที่โอน</span>
                        </div>
                      <input type="date" class="form-control" name="transfer_date" value="2018-01-01" required>
                    </div>
                    <div class="input-group">
                        <div class="input-group-prepend">
                          <span class="input-group-text w140px">เวลาที่โอน</span>
                        </div>
                      <input type="time" class="form-control" name="transfer_time" value="12:00" required>
                    </div>
                    <div class="input-group">
                        <div class="input-group-prepend">
                          <span class="input-group-text w140px">จำนวนเงิน (บาท)</span>
                        </div>
                      <input type="number" step="0.01" class="form-control" :value="order.place_price" name="transfer_amount" required>
                    </div>
                  </div>
                  <h3>⇩ อัพโหลดภาพ ⇩</h3>
                  <div class="input-group">
                      <div class="custom-file">
                        <input type="file" class="custom-file-input" id="transfer_pic" name="transfer_pic" accept="image/*" required @change="setFileName">
                        <label class="custom-file-label text-truncate" for="transfer_pic" id="lbl_transfer_pic">เลือกไฟล์ภาพ</label>
                      </div>
                  </div>
                  <div class="row">
                    <div class="col-12 mt-2 text-center">
                      <img id="previewImg" class="mt-3" style="max-height: 500px; max-width: 100%;">
                    </div>
                    <div v-if="is_previewImg" class="col-12 mt-2 text-center">
                      ตรวจสอบภาพหลักฐาน หากถูกต้องแล้ว<span class="text-danger">กดปุ่มแจ้งชำระเงิน</span>ได้เลย
                    </div>
                    <div v-if="!transfer_hide" class="alert alert-danger mt-2" role="alert" id="transfer_errors_box">
                      <strong><span id="transfer_errors" v-html="transfer_error"></span></strong>
                    </div>
                    <div class="col-12 mt-2 text-center">
                      <button class="btn btn-success" id="transfer_click" :disabled="is_disable">{{ is_disable ? 'กำลังบันทึก...' : 'แจ้งชำระเงิน' }}</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
            <template v-else>
              <div v-if="false" class="text-left">
                <h6 class="card-subtitle font-weight-bold">ธนาคารปลายทาง</h6>
                <p>{{ order.bank[order.payment_detail.target_bank] }}</p>
                <h6 class="card-subtitle font-weight-bold">วันที่โอน</h6>
                <p>{{ order.payment_detail.transfer_date }}</p>
                <h6 class="card-subtitle font-weight-bold">เวลาที่โอน</h6>
                <p>{{ order.payment_detail.transfer_time }}</p>
                <h6 class="card-subtitle font-weight-bold">จำนวนเงิน (บาท)</h6>
                <p>{{ order.payment_detail.transfer_amount }}</p>
              </div>
              <h6 class="card-subtitle font-weight-bold">หลักฐานการโอน</h6>
              <a :href="'/api/payment_file/' + order.order_code + '/'" target="_blank">
                <img class="mt-3" :src="'/api/payment_file/' + order.order_code + '/'" style="max-height: 500px; max-width: 100%;">
              </a>
            </template>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="row">
      <div class="col mt-2">
        <div class="alert alert-danger" role="alert">
          <span id="block_error"><strong>รายการสั่งซื้อถูกยกเลิกเนื่องจากไม่ชำระเงินในเวลาที่กำหนด</strong></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProfileForm from './profile-form.vue'

export default {
  name: 'Order',
  components: {ProfileForm},
  data () {
    return {
      order_code: this.$route.params.code,
      error: this.$route.query.error,
      is_disable: false,
      is_previewImg: false,
      transfer_error: '',
      transfer_hide: true,
      order: {
        order_code: '',
        status_class: '',
        status_str: '',
        tracking_no: '',
        delivery_str: '',
        delivery_fee: '',
        is_discount: false,
        discount: '',
        total_price: '',
        is_bill: false,
        is_book: false,
        is_sent: false,
        is_fail: false,
        name: '',
        email: '',
        address: {},
        address_str: '',
        bank: {},
        expire_str: '',
        place_price: '',
        product: [],
        payment_detail: {}
      }
    }
  },
  computed: {
    old_price_class () {
      return 0
    }
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getOrderInfo', code: this.order_code}).then(res => {
      let that = res.body
      if (that.fail) window.location.replace('/')
      this.order = that
    })
    window.onbeforeunload = function () {
      window.scrollTo(0, 0)
    }
  },
  mounted () {
  },
  methods: {
    setFileName () {
      document.getElementById('lbl_transfer_pic').innerHTML = document.getElementById('transfer_pic').value.replace(/.*(\/|\\)/, '')
      let reader = new FileReader()
      reader.onload = function (e) {
        document.getElementById('previewImg').src = e.target.result
      }
      reader.readAsDataURL(document.getElementById('transfer_pic').files[0])
      this.is_previewImg = true
    },
    submitTransfer () {
      this.is_disable = true
      this.$http.post('/api/vpost/', new FormData(document.getElementById('form_transfer')), {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        let that = res.body
        if (!that.success && (that.err_code === 'ERR_SIZE' || that.err_code === 'ERR_TYPE' || that.err_code === 'ERR_ADDR')) {
          this.transfer_error = that.err_code === 'ERR_SIZE' ? '<strong>ไฟล์มีขนาดเกิน 5 MB</strong>' : (that.err_code === 'ERR_TYPE' ? '<strong>อัพโหลดไฟล์ผิดประเภท</strong>' : '<strong>กรุณาระบุที่อยู่ให้ครบถ้วน</strong>')
          this.transfer_hide = false
          this.is_disable = false
        } else {
          location.reload(true)
        }
      })
    }
  }
}
</script>

<style scoped>
  .badge-primary{
    margin-top: 5px;
    white-space: normal;
  }
</style>



