<template>
  <div>
    <div v-show="!is_edit" class="card-body" id="info_address">
      <button v-if="!readonly" type="button" class="btn btn-outline-dark btn-sm mb-3" id="profile_edit_click" @click="editEnable">แก้ไข</button>
      <div v-show="!orderonly">
        <h6 class="card-subtitle font-weight-bold">ชื่อผู้ใช้</h6>
        <p>{{ !!name ? name : '' }}<span v-if="!name" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      </div>
      <h6 class="card-subtitle font-weight-bold">อีเมล<br><span class="text-danger">(ใช้เพื่อแจ้งเลขแทร็ก)</span></h6>
      <p>{{ !!email ? email : '' }}<span v-if="!email" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">ชื่อผู้รับของ</h6>
      <p>{{ !!address.name ? address.name : '' }}<span v-if="!address.name" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">เบอร์โทรศัพท์</h6>
      <p>{{ !!address.tel ? address.tel : '' }}<span v-if="!address.tel" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">บ้านเลขที่</h6>
      <p>{{ !!address.home ? address.home : '' }}<span v-if="!address.home" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">หมู่ ซอย ถนน<br>(เช่น <span class="text-primary">หมู่ 4</span>)</h6>
      <p>{{ !!address.place ? address.place : '' }}<span v-if="!address.place" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">ตำบล / แขวง</h6>
      <p>{{ !!address.subdistrict ? address.subdistrict : '' }}<span v-if="!address.subdistrict" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">อำเภอ / เขต</h6>
      <p>{{ !!address.district ? address.district : '' }}<span v-if="!address.district" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">จังหวัด</h6>
      <p>{{ !!address.province ? address.province : '' }}<span v-if="!address.province" class="text-danger">(ยังไม่ได้กรอก)</span></p>
      <h6 class="card-subtitle font-weight-bold">รหัสไปรษณีย์</h6>
      <p>{{ !!address.post ? address.post : '' }}<span v-if="!address.post" class="text-danger">(ยังไม่ได้กรอก)</span></p>
    </div>
    <div v-show="is_edit" class="card-body" id="form_address">
      <button type="button" class="btn btn-outline-dark btn-sm profile_save_click" :disabled="is_disable" @click="alertAddress">{{ is_disable ? 'กำลังบันทึก...' : 'บันทึก' }}</button>
      <button type="button" class="btn btn-outline-danger btn-sm profile_cancel_click" @click="editCancel">ยกเลิก</button>
      <div v-show="!orderonly">
        <h6 class="card-subtitle font-weight-bold mt-3">ชื่อผู้ใช้</h6>
        <input type="text" class="form-control mt-2" id="input_name">
      </div>
      <h6 class="card-subtitle font-weight-bold mt-3">อีเมล<br><span class="text-danger">(ใช้เพื่อแจ้งเลขแทร็ก)</span></h6>
      <input type="email" class="form-control mt-2" id="input_email">
      <h6 class="card-subtitle font-weight-bold mt-3">ชื่อผู้รับของ</h6>
      <input type="text" class="form-control mt-2" id="input_name_post">
      <h6 class="card-subtitle font-weight-bold mt-3">เบอร์โทรศัพท์</h6>
      <input type="tel" class="form-control mt-2" id="input_tel">
      <h6 class="card-subtitle font-weight-bold mt-3">บ้านเลขที่</h6>
      <input type="text" class="form-control mt-2" id="input_home">
      <h6 class="card-subtitle font-weight-bold mt-3">หมู่ ซอย ถนน<br>(เช่น <span class="text-primary">หมู่ 4</span>)</h6>
      <input type="text" class="form-control mt-2" id="input_place">
      <h6 class="card-subtitle font-weight-bold mt-3">ตำบล / แขวง</h6>
      <input type="text" class="form-control mt-2" id="input_subdistrict">
      <h6 class="card-subtitle font-weight-bold mt-3">อำเภอ / เขต</h6>
      <input type="text" class="form-control mt-2" id="input_district">
      <h6 class="card-subtitle font-weight-bold mt-3">จังหวัด</h6>
      <input type="text" class="form-control mt-2" id="input_province">
      <h6 class="card-subtitle font-weight-bold mt-3">รหัสไปรษณีย์</h6>
      <input type="text" class="form-control mt-2" id="input_post">
      <button type="button" class="btn btn-outline-dark btn-sm mt-2 profile_save_click" :disabled="is_disable" @click="alertAddress">{{ is_disable ? 'กำลังบันทึก...' : 'บันทึก' }}</button>
      <button type="button" class="btn btn-outline-danger btn-sm mt-2 profile_cancel_click" @click="editCancel">ยกเลิก</button>
    </div>
    <div v-if="is_alert" id="addressModal" class="modal d-block">
      <div class="modal-content">
        <div class="modal-header d-block text-center pt-2">
          <h4>ยืนยันการแก้ไข</h4>
        </div>
        <div class="modal-body pt-3">
          <p><strong>นี่คือที่อยู่ใหม่ของคุณ</strong></p>
          <hr>
          <p>กรุณาส่ง {{ pre_name_tel }}</p>
          <p>{{ pre_address }}</p>
        </div>
        <div class="modal-footer d-block text-center p-2">
          <button class="btn btn-success" :disabled="is_disable" @click="editProfile">{{ is_disable ? 'กำลังบันทึก...' : 'ยืนยัน' }}</button>
          <button class="btn btn-danger alert_close" @click="closeAddress">ยกเลิก</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'profile-form',
  props: ['name', 'email', 'address', 'readonly', 'orderonly'],
  data () {
    return {
      is_edit: false,
      is_disable: false,
      is_alert: false,
      pre_name_tel: '',
      pre_address: ''
    }
  },
  computed: {
    statusClass () {
      return 0
    }
  },
  created () {
    let elm = document.getElementById('scriptAutoAddress')
    if (elm != null) {
      elm.parentNode.removeChild(elm)
    }
    let jsScript = document.createElement('script')
    jsScript.async = true
    jsScript.setAttribute('src', '/assets/js/autoAddress.js')
    jsScript.setAttribute('id', 'scriptAutoAddress')
    document.body.appendChild(jsScript)
  },
  mounted () {
  },
  methods: {
    alertAddress () {
      let inPost = document.querySelector('#input_post').value
      let isBKK = inPost[0] === '1' && inPost[1] === '0'
      this.pre_name_tel = document.querySelector('#input_name_post').value + ' ' + document.querySelector('#input_tel').value
      this.pre_address = document.querySelector('#input_home').value + ' ' + document.querySelector('#input_place').value + ' ' + (isBKK ? ' แขวง' : ' ต.') + document.querySelector('#input_subdistrict').value + (isBKK ? ' เขต' : ' อ.') + document.querySelector('#input_district').value + (isBKK ? ' ' : ' จ.') + document.querySelector('#input_province').value + ' ' + inPost
      this.is_alert = true
    },
    closeAddress () {
      this.is_alert = false
    },
    editEnable () {
      document.querySelector('#input_name').value = this.name
      document.querySelector('#input_email').value = this.email
      document.querySelector('#input_name_post').value = this.address.name
      document.querySelector('#input_tel').value = this.address.tel
      document.querySelector('#input_home').value = this.address.home
      document.querySelector('#input_place').value = this.address.place
      document.querySelector('#input_subdistrict').value = this.address.subdistrict
      document.querySelector('#input_district').value = this.address.district
      document.querySelector('#input_province').value = this.address.province
      document.querySelector('#input_post').value = this.address.post
      this.is_edit = true
    },
    editProfile () {
      this.is_disable = true
      let name = document.querySelector('#input_name').value
      let email = document.querySelector('#input_email').value
      let address = JSON.stringify({
        name: document.querySelector('#input_name_post').value,
        tel: document.querySelector('#input_tel').value,
        home: document.querySelector('#input_home').value,
        place: document.querySelector('#input_place').value,
        subdistrict: document.querySelector('#input_subdistrict').value,
        district: document.querySelector('#input_district').value,
        province: document.querySelector('#input_province').value,
        post: document.querySelector('#input_post').value
      })
      this.$http.post('/api/vpost/', {action: 'editProfile', name: name, email: email, address: address}).then(res => location.reload(true))
    },
    editCancel () {
      location.reload(true)
    }
  }
}
</script>

<style scoped>
  .modal {
      position: fixed;
      z-index: 3;
      padding-top: 100px;
      padding-bottom: 15px;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      overflow: auto;
      background-color: rgb(0,0,0);
      background-color: rgba(0,0,0,0.4);
  }
  .modal-content {
      position: relative;
      background-color: #fefefe;
      margin: auto;
      padding: 0;
      border: 1px solid #888;
      width: 95%;
      max-width: 720px;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
      -webkit-animation-name: animatetop;
      -webkit-animation-duration: 0.4s;
      animation-name: animatetop;
      animation-duration: 0.4s
  }
  @-webkit-keyframes animatetop {
      from {top:-300px; opacity:0}
      to {top:0; opacity:1}
  }
  @keyframes animatetop {
      from {top:-300px; opacity:0}
      to {top:0; opacity:1}
  }
  .modal-header {
      padding: 2px 16px;
      background-color: rgb(220, 53, 69);
      color: white;
  }
  .modal-body {padding: 2px 16px;}
  .modal-footer {
      padding: 2px 16px;
      background-color: rgba(0, 0, 0, 0.05);
      color: white;
  }
</style>



