<template>
  <div id="app">
    <nav class="navbar navbar-expand fixed-top color_nav trans_top" :style="{background: navBg, boxShadow: navShadow}">
        <div class="navbar-collapse collapse w-100 order-1 order-md-0 dual-collapse2">
            <div class="navbar-nav mx-auto ml-md-0">
              <a href="/">
                <img v-if="!fb_white || !is_landing" class="d-none d-md-block" src="./assets/logo.png" height="26">
                <img class="d-block d-md-none ml-2" src="./assets/logo_dark.png" height="58">
              </a>
            </div>
        </div>
        <div class="mx-auto order-0 w-100">
            <ul class="navbar-nav mx-auto">
                <form class="form-inline w-100" action="/search/">
                    <div class="input-group w-100">
                        <button class="btn btn-danger rounded-0 my-sm-0" type="submit">&#8981;</button>
                        <input name="query" type="search" class="form-control rounded-0" id="search_input" :style="{backgroundColor: 'rgba(255,' + (fb_white ? '255' : '200') + ',' + (fb_white ? '255' : '200') + ',0.5)'}" placeholder="ค้นหา" aria-label="search" aria-describedby="basic-addon1">
                    </div>
                </form>
            </ul>
        </div>
        <div class="navbar-collapse collapse w-100 order-3 dual-collapse2">
            <ul class="navbar-nav ml-auto">
                <li v-if="countCart!=null&&countCart>0" class="nav-item margin_right15 d-none d-sm-block ml-2">
                    <a href="/cart/">
                        <button id="badge_cart" type="button" class="btn btn-outline-light rounded-0"><img src="/assets/img/nav/cart_icon.png" height="20"> <span class="badge badge-light">{{ countCart }}</span>
                        </button></a>
                </li>
                <li class="nav-item ml-2" style="margin-right: -10px;">
                <template v-if="username!=null">
                    <a class="btn btn-outline-light rounded-0 d-none d-sm-block" href="/profile/">{{ username }}</a>
                    <a class="btn btn-outline-light rounded-0 d-block d-sm-none" href="/profile/">ข้อมูลการซื้อ</a>
                </template>
                <template v-else>
                    <div class="hover_pointer" @click="fbLogin" id="login_button">
                        <img src="./assets/fb_white.png" height="28"> <span class="text-light">ล็อกอิน</span>
                    </div>
                </template>
                </li>
            </ul>
        </div>
        <a v-if="countCart!=null&&countCart>0" href="/cart/"><button id="badge_cart" type="button" class="btn btn-light d-block d-sm-none p-0 trans_top">
          <img src="/assets/img/nav/cart_icon.png" height="20"><span class="badge badge-light">{{ countCart }}</span>
        </button></a>
    </nav>

    <router-view/>

    <div id="footer" class="container-fluid bg-danger">
      <div class="row pt-2 pb-2 text-light">
        <div class="col-12 col-lg-auto">
          <div class="row">
            <div class="col d-block d-lg-none"></div>
            <div class="col-auto text-light"><p class="d-none d-lg-block"></p>© 2018 Idolm.art ALL RIGHTS RESERVED</div>
            <div class="col d-block d-lg-none"></div>
          </div>
        </div>
        <div class="col d-none d-lg-block"></div>
        <div class="col-12 col-lg-auto">
          <div class="row">
            <div class="col d-block d-lg-none"></div>
            <div class="col-auto"><div id="Certificate-banners"><img src="./assets/logo.png" style="height: 26px; margin: 14px 0 14px 0;"></div></div>
            <div class="col d-block d-lg-none"></div>
          </div>
        </div>
        <div class="col d-none d-lg-block"></div>
        <div class="col-12 col-lg-auto">
          <div class="row">
            <div class="col d-block d-lg-none"></div>
            <div class="col-auto"><a href="/policy/" class="text-light"><p class="d-none d-lg-block"></p>นโยบายความเป็นส่วนตัว</a> | <a href="/about/" class="text-light">ข้อมูลร้าน</a> | <a href="/terms/" class="text-light">การคืนเงิน</a></div>
            <div class="col d-block d-lg-none"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  head: {
    title () {
      return {
        inner: 'Idolm.art',
        separator: ' ',
        complement: ' '
      }
    },
    link: [
      {rel: 'shortcut icon', href: '/assets/img/nav/logo_pink.png'}
    ]
  },
  data () {
    return {
      username: null,
      countCart: null,
      scrollY: 0,
      iScrollPos: 0,
      isNavHide: false,
      arrScript: [
        // {src: '/assets/js/jquery.js', id: 'scriptJquery'},
        // {src: '/assets/js/popper.js', id: 'scriptPopper'},
        // {src: '/assets/js/bootstrap.js', id: 'scriptBootstrap'},
        // {src: '/assets/js/bcSwipe.js', id: 'scriptBcSwipe'},
        // {src: '/assets/js/idolmart.js', id: 'scriptIdolmart'},
        // {src: '/assets/js/fblogin.js', id: 'scriptFBlogin'}
      ]
    }

    // if (this.$route.name === 'Product') this.arrScript.push({src: '/assets/js/product.js'})
    // let nextJs = (i) => {
    //   if (i >= this.arrScript.length) return
    //   let jsScript = document.createElement('script')
    //   jsScript.onload = () => nextJs(i + 1)
    //   jsScript.async = true
    //   jsScript.setAttribute('src', this.arrScript[i].src)
    //   jsScript.setAttribute('id', this.arrScript[i].id)
    //   document.body.appendChild(jsScript)
    // }
    // nextJs(0)
  },
  created () {
    this.$http.post('/api/vpost/', {action: 'getNavInfo'}).then(res => {
      this.username = res.body.username
      this.countCart = res.body.countCart
    })
    window.addEventListener('scroll', this.handleScroll)

    let elm = document.getElementById('dbd-init')
    if (elm != null) {
      elm.parentNode.removeChild(elm)
    }
    let jsScript = document.createElement('script')
    jsScript.async = true
    jsScript.setAttribute('src', 'https://www.trustmarkthai.com/callbackData/initialize.js?t=39097d4779-38-5-5a52f35d4f62235a7a9a4bf0234b50808ab')
    jsScript.setAttribute('id', 'dbd-init')
    document.body.appendChild(jsScript)
  },
  updated () {
    // let pullDis = window.innerHeight - document.getElementById('footer').clientHeight - document.getElementById('footer').offsetTop
    // console.log(pullDis, window.innerHeight, document.getElementById('footer').clientHeight, document.getElementById('footer').offsetTop)
    // document.getElementById('footer').style.marginTop = pullDis > 0 ? pullDis + 'px' : 0

    let elm = document.getElementById('dbd-init')
    if (elm != null) {
      elm.parentNode.removeChild(elm)
    }
    let jsScript = document.createElement('script')
    jsScript.async = true
    jsScript.setAttribute('src', 'https://www.trustmarkthai.com/callbackData/initialize.js?t=39097d4779-38-5-5a52f35d4f62235a7a9a4bf0234b50808ab')
    jsScript.setAttribute('id', 'dbd-init')
    document.body.appendChild(jsScript)
  },
  methods: {
    fbLogin () {
      window.loginFB(window.location)
    },
    handleScroll () {
      this.scrollY = window.scrollY
      if (this.scrollY - this.iScrollPos > 58 && this.scrollY > 58 && !this.isNavHide) {
        document.querySelector('.navbar.trans_top').style.top = '-60px'
        let cart = document.querySelector('#badge_cart.d-sm-none')
        if (cart != null) cart.style.top = '2px'
        this.isNavHide = true
        this.iScrollPos = this.scrollY
      } else if ((this.scrollY - this.iScrollPos < -58 && this.isNavHide) || this.scrollY < 58) {
        document.querySelector('.navbar.trans_top').style.top = '0'
        let cart = document.querySelector('#badge_cart.d-sm-none')
        if (cart != null) cart.style.top = '60px'
        this.isNavHide = false
        this.iScrollPos = this.scrollY
      }
      if (Math.abs(this.iScrollPos - this.scrollY) > 58) this.iScrollPos = this.scrollY
    }
  },
  computed: {
    navBg () {
      if (this.$route.name !== 'Landing') return '#dc3545'
      return 'rgba(220,53,69,' + (this.scrollY > 500 ? 0.95 : this.scrollY / 527) + ')'
    },
    navShadow () {
      if (this.$route.name !== 'Landing') return 'rgba(0,0,0,0.3)'
      return '0 1px 8px rgba(0,0,0,' + (this.scrollY > 500 ? 0.3 : this.scrollY / 1667) + ')'
    },
    fb_white () {
      if (this.$route.name !== 'Landing') return true
      return this.scrollY <= 200
    },
    is_landing () {
      return this.$route.name === 'Landing'
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style>
  @import '/assets/bootstrap.css'
</style>
<style>
  @import '/assets/jquery.css'
</style>
<style>
  @import '/assets/idolmart.css'
</style>



